<template>
<div  >
<!--  设备信息样本-->
<!--  {{this.tmpCaption}}-->
<!--  {{this.pri_detail.treeNodeOrgId}}-->

  <el-button @click="genLogicNo">生成UUID</el-button>
<!--  <el-button style="float:right;right:100px;" type="primary" @click="showCb">检测</el-button>-->
<!--  <query-valve-current-data-for-sa ref="query1"></query-valve-current-data-for-sa>-->

</div>
</template>

<script>

import Bus from "../../../../utils/Bus.js"
// import QueryValveCurrentDataForSa from "../../deviceManager/QueryValveCurrentDataForSa";

export default {
  name: "DeviceInfoBank2",
  // components:{'QueryValveCurrentDataForSa':QueryValveCurrentDataForSa},

  mounted(){
    console.info("Mounted_DeviceInfoBank2:"+this.pri_detail.treeNodeOrgId);
    this.getQueryDetail();
  },
  created(){
    console.info("Created_DeviceInfoBank2:"+this.pri_detail.treeNodeOrgId);
    console.info("Created_DeviceInfoBank2.treeNodeIds:"+this.pri_detail.treeNodeIds);
    console.info("Created_DeviceInfoBank2.treeNodeType:"+this.pri_detail.treeNodeType);
    console.info("Created_DeviceInfoBank2.currentTreeNode:"+JSON.stringify(this.pri_detail.currentTreeNode));
    console.info("Created_DeviceInfoBank2.cbInfo:"+JSON.stringify(this.pri_detail.cbInfo));
  },
  props: {

    pri_detail: {
      type: Object,
      default: function () {
        return {
          treeNodeCaptions:[],
          treeNodeIds:[],
          treeNodeOrgId:'',
          currentTreeNode:{},
          treeNodeType:[],
          cbInfo: {},
        }

      }
    }
  },
  watch:{
    pri_detail:function(newVal,oldVal){
      console.info("watch_DeviceInfoBank2点击树节点查询【设备信息2】新值:"+newVal.treeNodeOrgId+",旧值："+oldVal.treeNodeOrgId);
      console.info("watch_DeviceInfoBank2点击树节点查询【设备信息2】新值:"+newVal.treeNodeIds+",旧值："+oldVal.treeNodeIds);
      console.info("watch_DeviceInfoBank2点击树节点查询【设备信息2】当前树节点新值:"+JSON.stringify(this.pri_detail.currentTreeNode));
      console.info("watch_DeviceInfoBank2点击树节点查询【设备信息2】type值:"+this.pri_detail.treeNodeType);
      console.info("watch_DeviceInfoBank2点击树节点查询【设备信息2】type值:"+JSON.stringify(this.pri_detail.cbInfo));
      if(newVal.treeNodeOrgId!==oldVal.treeNodeOrgId){
       //查询

        this.tmpCaption = [];
        for(var i1=0;i1< this.pri_detail.treeNodeCaptions.length;i1++){
          this.tmpCaption[i1]= this.customGlobal.decrypt(this.pri_detail.treeNodeCaptions[i1],this.customGlobal.cryptoKey);

          /////////
          // this.$refs.query1.treeNodeInfo.treeNodeOrgId = newVal.treeNodeOrgId;
          // this.$refs.query1.treeNodeInfo.treeNodeCaptions = this.pri_detail.treeNodeCaptions;
          // this.$refs.query1.reQueryValveCurrentData() ;
          // ////////

        }
        console.info(this.tmpCaption);

      }else if(newVal.cbInfo!==oldVal.cbInfo){
        // console.info("原楼栋信息["+JSON.stringify(oldVal)+"],新楼栋信息["+JSON.stringify(newVal)+"]，请处理。");
        console.info("XX信息发生变化，请处理。");
      }
    }
  },

  data(){

    return {
      tmpCaption: [],

    }
  },
  methods:{
    showCb(){
      let data = {};
      // console.info("返回对象："+JSON.stringify(  this.$refs.cb.showPublicInfo() ));
      this.$emit("parent_method", "", val => {
          console.info("showCb_obj:"+ JSON.stringify(val) );
          data = val;
        }
      );

    },
    genLogicNo(){
      console.info("生成UUID:"+this.customGlobal.genLogicNo("DEVICE"));
    },


    getQueryDetail: function(){

      if(this.pri_detail.treeNodeOrgId!==undefined){
        console.info("点击Tab标签页OrgInfoBank2查询【设备信息2】,获取值:"+this.pri_detail.treeNodeOrgId);
        //查询
        this.tmpCaption = [];
        for(var i1=0;i1< this.pri_detail.treeNodeCaptions.length;i1++){
          this.tmpCaption[i1]= this.customGlobal.decrypt(this.pri_detail.treeNodeCaptions[i1],this.customGlobal.cryptoKey);
        }
        console.info(this.tmpCaption);
        //////////////
        // this.$refs.query1.treeNodeInfo.treeNodeOrgId = this.pri_detail.treeNodeOrgId;
        // this.$refs.query1.treeNodeInfo.treeNodeCaptions = this.pri_detail.treeNodeCaptions;
        // this.$refs.query1.queryValveCurrentData() ;

      }else{
        console.info("当前没有选择任何组织机构ID，查询所有[设备信息2]");
      }
    }
  }
}
</script>

